import React from 'react';

// 
const HeaderRouter = ({allData, states}) => {
	return (
		<>
			{ // allData
				allData.map(pageData => {
					// pageData(children)
					if (pageData.children === void 0) {
						return (
							<React.Fragment key={`page${pageData.state.page}`}>
								{window.location.pathname === pageData.path && pageData.name}
							</React.Fragment>
						);
					}

					// pageData(children)
					else {
						return (
							<React.Fragment key={`page${pageData.state.page}`}>
								{ // (children)pageData
									pageData.children.map(pageDataWithC => {
										return (
											<React.Fragment key={`page${pageDataWithC.state.page}`}>
												{window.location.pathname === pageDataWithC.path && pageDataWithC.name}
											</React.Fragment>
										);
									})
								}
							</React.Fragment>
						);
					}
				})
			}
		</>
	);
}

export default HeaderRouter;
