import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';

// 
const PageRouter = ({allData, states}) => {
	return (
		<>
			{ // allData
				allData.map(pageData => {
					// pageData(children)
					if (pageData.children === void 0) {
						return (
							<React.Fragment key={`page${pageData.state.page}`}>
								<Router basename={process.env.PUBLIC_URL}>
									{console.log(pageData.path)}
									<Route path={pageData.path} render={()=> <pageData.Component title={pageData.name} allData={allData} />} />
								</Router>
							</React.Fragment>
						);
					}

					// pageData(children)
					else {
						return (
							<React.Fragment key={`page${pageData.state.page}`}>
								{ // (children)pageData
									pageData.children.map(pageDataWithC => {
										return (
											<React.Fragment key={`page${pageDataWithC.state.page}`}>
												<Router basename={process.env.PUBLIC_URL}>
													{/* {console.log(pageDataWithC)} */}
													<Route path={pageDataWithC.path} render={()=> <pageDataWithC.Component title={pageDataWithC.name} allData={allData} />} />
												</Router>
											</React.Fragment>
										);
									})
								}
							</React.Fragment>
						);
					}
				})
			}
		</>
	);
}

export default PageRouter;
